<template>
<div class="container">

    <div v-if="showDescription">
        <div class="about-exercise">
            <h1>Привет! Это мой pet-project</h1>
            <p>Это небольшая игра, развивающая ваши математические навыки</p>
            <p>Перед вами с определенным интервалом будут появляться числа, вам нужно будет в уме посчитать их сумму</p>
            <p>Интервал и количество цифр вы можете выбрать сами</p>
        </div>

        <div class="exercise-setings">
            <form class="setings-form" v-on:submit.prevent="onSubmit">
                <div class="setings-form__wrap">
                    <div class="setings-form__list">
                        <p>Выберите количество цифр</p>
                        <label class="setings-form__list-item"><input required v-model="number" checked name="number" type="radio" value="5"> 5</label>
                        <label class="setings-form__list-item"><input required v-model="number" checked name="number" type="radio" value="10"> 10</label>
                        <label class="setings-form__list-item"><input required v-model="number" name="number" type="radio" value="20"> 20</label>
                        <label class="setings-form__list-item"><input required v-model="number" name="number" type="radio" value="30"> 30</label>
                    </div>
                    <div class="setings-form__list">
                        <p>Выберите интервал появления цифр</p>
                        <label class="setings-form__list-item"><input required v-model="second" checked name="second" type="radio" value="3000"> 3</label>
                        <label class="setings-form__list-item"><input required v-model="second" checked name="second" type="radio" value="2000"> 2</label>
                        <label class="setings-form__list-item"><input required v-model="second" name="second" type="radio" value="1000"> 1</label>
                        <label class="setings-form__list-item"><input required v-model="second" name="second" type="radio" value="500"> 0.5</label>
                    </div>
                </div>

                <button>Старт</button>
            </form>
        </div>
    </div>

    <div v-if="!showDescription">
        <div class="exercise-numbers">
            <div class="exercise-numbers__item" v-for="(number, index) of numbers" :key="index">
                {{ number }}
            </div>
        </div>

        <div class="exercise-answer" v-show="disableInterface">
            <label>Ваш ответ:
                <input type="number" v-model="userAnswer">
            </label>
            <button @click="checkAnswer()">ОК</button>
        </div>

        <div class="answer-check" v-show="rightAnswer !== ''">
            <p v-if="rightAnswer">
                Верно
            </p>
            <p v-else>
                Неверно
            </p>

            <button @click="restart()">Попробовать ещё раз</button>

        </div>
    </div>

</div>
</template>

<script>
export default {
    data() {
        return {
            number: '',
            second: '',
            numbers: [],
            showDescription: true,
            userAnswer: '',
            arraySum: '',
            rightAnswer: '',
            disableInterface: false,
        }
    },
    methods: {
        onSubmit() {
            let number = this.number,
                numbers = this.numbers,
                second = this.second;

            function randomInteger(min, max) {
                let rand = min - 0.5 + Math.random() * (max - min + 1);
                if (rand > 1) {
                    return '+' + Math.round(rand);
                } else {
                    return Math.round(rand);
                }

            }

            numbers.push(randomInteger(-number, number));
            for (let i = 0; i < number - 1; i++) {
                let addElements = () => {
                    numbers.push(randomInteger(-number, number));
                    if(i == number - 2) {
                        this.disableInterface = true;
                    }
                }

                setTimeout(addElements, (i + 1) * second);
            }

            this.showDescription = false;

        },

        checkAnswer() {
            let numbers = this.numbers,
                sum = 0;

            for (let i = 0; i < numbers.length; i++) {
                sum += Number(numbers[i]);
            }

            this.arraySum = sum;

            console.log(this.arraySum, this.userAnswer);

            if (this.arraySum == this.userAnswer) {
                this.rightAnswer = true;
            } else {
                this.rightAnswer = false;
            }
        },

        restart() {
            this.numbers = [];
            this.showDescription = true;
            this.userAnswer = '';
            this.arraySum = '';
            this.rightAnswer = '';
        }

    },
}
</script>

